

.menuBar {
    width:100%;
    height:180px;
    background:#002c49;
    color:#fff;
    display:flex;
    align-items: center;
    justify-content: center;
    font-size:4vh;
    font-weight:bold;
}

.subWrapper {
    width:100%;
    height:calc(1920px - 180px);
    position:absolute;
    left:0;
    top:180px;
    background:#fff;
}
.mainNotice {
    width:100%;
    height:200px;
    display:flex;
    align-items: center;
    justify-content: center;
    background:#119891;
    color:#fff;
    font-size:3vh;
    font-weight:bold;
}
.mainButtonBox {
    width:100%;
    box-sizing:border-box;
    padding:60px 0;
    height:calc(1920px - 180px - 200px - 608px);
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.mainButton {
    width:900px;
    height:250px;
    padding-left:10%;
    margin:0px auto;
    box-sizing:border-box;
    background:#333;
    color:#fff;
    font-size:4.5vh;
    display:flex;
    align-items: center;
    justify-content: space-around;
    font-weight:bold;
    letter-spacing:-3px;
}
.mainButton img{
    position:absolute;
    left:15%;
}
.bgGreen {
    letter-spacing:34px;
    padding-left:13%;
    background-color:#119891;
}
.bgBlue {
    background-color:#1c78d5;    
}
.bgRed {
    background-color:#e10f17;  
}



.homeShip {
    width:10vw;
    height:10vw;
    background-color:#002c49;
    background-image:url('/public/images/ship.png');
    background-size:contain;
    position:absolute;
    left:5vw;
    top:2vh;
    border:none;
}
.homeButton {
    width:10vw;
    height:10vw;
    background-color:#002c49;
    background-image:url('/public/images/home.png');
    background-size:contain;
    position:absolute;
    left:5vw;
    top:2vh;
    border:none;
}
.tabWrapper {
    width:100%;
    display:flex;
    align-items:center;
    justify-content: baseline;
    background:#84c6cb;
    color:#fff;
    font-size:40px;
}
.tabWrapper .isActive {
    background:#119891;
    color:#fff;
	cursor:default;
}

.roomButtonWrap {
    padding:30px 0;
}
.roomButtonWrap button{
    width:20%;
    padding:30px 0;
    border:#fff 2px solid;
    box-sizing:border-box;
    font-size:30px;
	flex-wrap: wrap;
}
.roomButtonWrap .isActive{
    background:#333;
    color:#fff;
}
.floorImg {
    width:100%;
}
.mapWrapper {
    width:1000px;
    display:block;
    margin:0px auto;
    position:relative;
}
.mapMaker {
    width:80px;
    height:90px;
    background-image:url('/public/images/marker.png');
    background-size:contain;
    background-repeat:no-repeat;
    position:absolute;
    left:0;
    top:0;
    animation: blinker infinite 1s;
    z-index:100;
}
.roomCondition {
    width:25px;
    height:25px;
    position:absolute;
    background:#f0f0f0;
    left:10px;
    top:10px;
    border-radius:50%;
}

@keyframes blinker {
    0% {
        opacity:0;
    }
    50%{
        opacity:1;
    }
    100%{
        opacity:0;
    }
}